import onAction from './handlers/onAction';
import createOnCangjieInput from './handlers/onCangjieInput';
import { key } from '../utils/meta';
import type { MobileBiFactory } from '@ali/4ever-cangjie';
import { commands } from './commands';
import { queries } from './queries';
import { actions } from './actions';

export const mobileBiFactory: MobileBiFactory = (configs) => {
  return {
    key,
    commands,
    queries,
    onCangjieInput: createOnCangjieInput(),
    actions,
    onAction,
    models: {
      klass: 'mark',
      type: 'highlight',
    },
  }
}
